import React, { useState, useEffect } from 'react';
import { fetchCategories, subscribeUser, testKeywordMatch } from '../services/api';
import { isConfigured, getOpenAIKey } from '../services/localData';
import { Link } from 'react-router-dom';
import { Alert } from 'react-bootstrap';
import './Subscribe.css';

function Subscribe() {
  const [email, setEmail] = useState('');
  const [keywords, setKeywords] = useState('');
  const [selectedCategories, setSelectedCategories] = useState(['cs']);
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState(null);
  const [messageType, setMessageType] = useState('');
  const [testResults, setTestResults] = useState([]);
  const [showTestResults, setShowTestResults] = useState(false);
  const [useOpenAI, setUseOpenAI] = useState(true);
  const [openAIConfigured, setOpenAIConfigured] = useState(false);
  const [emailConfigured, setEmailConfigured] = useState(false);

  useEffect(() => {
    // 获取分类列表
    const getCategories = async () => {
      try {
        const data = await fetchCategories();
        setCategories(data);
      } catch (err) {
        console.error('Error fetching categories:', err);
      }
    };
    
    getCategories();
    
    // 检查OpenAI API密钥是否已配置
    const checkOpenAIConfig = () => {
      const apiKey = getOpenAIKey();
      console.log('OpenAI API密钥状态:', apiKey ? '已配置' : '未配置');
      setOpenAIConfigured(!!apiKey);
    };
    
    // 检查邮箱设置是否已配置
    const checkEmailConfig = () => {
      const emailSettings = localStorage.getItem('email_settings');
      setEmailConfigured(!!emailSettings);
    };
    
    checkOpenAIConfig();
    checkEmailConfig();
    
    // 设置定时器，每5秒检查一次配置状态
    const configCheckInterval = setInterval(() => {
      checkOpenAIConfig();
      checkEmailConfig();
    }, 5000);
    
    return () => {
      clearInterval(configCheckInterval);
    };
  }, []);

  const handleCategoryChange = (categoryId) => {
    setSelectedCategories(prev => {
      if (prev.includes(categoryId)) {
        return prev.filter(id => id !== categoryId);
      } else {
        return [...prev, categoryId];
      }
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!email || !keywords) {
      setMessage('请填写所有必填字段');
      setMessageType('error');
      return;
    }
    
    if (selectedCategories.length === 0) {
      setMessage('请至少选择一个论文分类');
      setMessageType('error');
      return;
    }
    
    const keywordsList = keywords
      .split(',')
      .map(k => k.trim())
      .filter(k => k.length > 0);
    
    if (keywordsList.length === 0) {
      setMessage('请输入有效的关键词');
      setMessageType('error');
      return;
    }
    
    setLoading(true);
    try {
      await subscribeUser(email, keywordsList, selectedCategories);
      setMessage('订阅成功！您将收到匹配关键词的论文推送');
      setMessageType('success');
      
      // 清空表单
      setEmail('');
      setKeywords('');
      setSelectedCategories(['cs']);
    } catch (err) {
      console.error('Subscription error:', err);
      setMessage(err.message || '订阅失败，请稍后再试');
      setMessageType('error');
    } finally {
      setLoading(false);
    }
  };

  const handleTestKeywords = async () => {
    if (!keywords) {
      setMessage('请输入关键词进行测试');
      setMessageType('error');
      return;
    }
    
    // 检查OpenAI API密钥是否已配置（如果用户选择使用AI增强匹配）
    if (useOpenAI && !openAIConfigured) {
      setMessage('使用AI增强匹配需要先配置OpenAI API密钥，请前往设置页面进行配置');
      setMessageType('error');
      return;
    }
    
    const keywordsList = keywords
      .split(',')
      .map(k => k.trim())
      .filter(k => k.length > 0);
    
    setLoading(true);
    try {
      console.log('测试关键词:', keywordsList, '使用OpenAI:', useOpenAI && openAIConfigured);
      
      // 如果选择了使用OpenAI但未配置，则回退到不使用OpenAI
      const useAI = useOpenAI && openAIConfigured;
      
      const response = await testKeywordMatch(keywordsList, useAI);
      setTestResults(response);
      setShowTestResults(true);
      setMessage(`关键词测试成功${useAI ? '(使用AI增强匹配)' : '(使用简单关键词匹配)'}`);
      setMessageType('success');
    } catch (err) {
      console.error('Keyword test error:', err);
      setMessage(err.message || '关键词测试失败，请稍后再试');
      setMessageType('error');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="subscribe-page">
      <h1>订阅论文推送</h1>
      
      {message && (
        <div className={`message ${messageType}`}>
          {message}
        </div>
      )}
      
      {useOpenAI && !openAIConfigured && (
        <Alert variant="warning" className="mb-4">
          您尚未配置OpenAI API密钥。
          <Link to="/settings" className="alert-link mx-1">前往设置</Link>
        </Alert>
      )}
      
      <div className="subscribe-container">
        <div className="subscribe-form-container">
          <form onSubmit={handleSubmit} className="subscribe-form">
            <div className="form-group">
              <label htmlFor="email">邮箱地址 *</label>
              <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder="your.email@example.com"
                required
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="keywords">关键词 *</label>
              <textarea
                id="keywords"
                value={keywords}
                onChange={(e) => setKeywords(e.target.value)}
                placeholder="输入关键词，用逗号分隔，如：机器学习, 神经网络, 深度学习"
                required
              />
              <small>多个关键词请用逗号分隔</small>
            </div>
            
            <div className="form-group">
              <label>论文分类 *</label>
              <div className="categories-container">
                {categories.map(category => (
                  <div key={category.id} className="category-checkbox">
                    <input
                      type="checkbox"
                      id={`category-${category.id}`}
                      checked={selectedCategories.includes(category.id)}
                      onChange={() => handleCategoryChange(category.id)}
                    />
                    <label htmlFor={`category-${category.id}`}>{category.name}</label>
                  </div>
                ))}
              </div>
            </div>
            
            <div className="form-group checkbox-group">
              <label className="checkbox-container">
                <input
                  type="checkbox"
                  checked={useOpenAI}
                  onChange={() => setUseOpenAI(!useOpenAI)}
                />
                <span className="checkmark"></span>
                使用AI增强匹配（需要OpenAI API密钥）
              </label>
              {useOpenAI && !openAIConfigured && (
                <div className="checkbox-note">
                  <Alert variant="warning" className="py-2">
                    您尚未配置OpenAI API密钥。
                    <Link to="/settings" className="alert-link mx-1">前往设置</Link>
                  </Alert>
                </div>
              )}
            </div>
            
            <div className="form-actions">
              <button 
                type="button" 
                className="btn btn-secondary" 
                onClick={handleTestKeywords}
                disabled={loading}
              >
                {loading ? '测试中...' : '测试关键词'}
              </button>
              <button 
                type="submit" 
                className="btn btn-primary"
                disabled={loading}
              >
                {loading ? '订阅中...' : '订阅'}
              </button>
            </div>
          </form>
        </div>
        
        {showTestResults && testResults.length > 0 && (
          <div className="test-results-container">
            <h3>关键词测试结果</h3>
            <p>找到 {testResults.length} 篇匹配论文。 {useOpenAI && openAIConfigured ? '(使用AI增强匹配)' : '(使用简单关键词匹配)'}</p>
            <div className="papers-list">
              {testResults.map((paper, index) => (
                <div key={index} className="paper-card">
                  <h4>
                    <a href={paper.link} target="_blank" rel="noreferrer">{paper.title}</a>
                  </h4>
                  <p className="authors">作者: {paper.authors.join(', ')}</p>
                  <p className="date">发布日期: {new Date(paper.published).toLocaleDateString()}</p>
                  {paper.matched_keywords && (
                    <p className="matched-keywords">
                      匹配关键词: {paper.matched_keywords.join(', ')}
                    </p>
                  )}
                  <p className="summary">{paper.summary.substring(0, 200)}...</p>
                </div>
              ))}
            </div>
          </div>
        )}
        
        {showTestResults && testResults.length === 0 && (
          <div className="test-results-container empty-results">
            <h3>关键词测试结果</h3>
            <p>未找到匹配论文。您可以尝试使用更广泛的关键词或选择不同的分类。</p>
          </div>
        )}
      </div>
    </div>
  );
}

export default Subscribe; 